.filterContainer {
    display: flex;
    justify-content: space-between;
    width: 500px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 50px;
  }
  
  .filterLabel {
    width: 450px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: auto;
    margin-left: auto;
    font-size: 18px;
    font-family: system-ui;
    font-weight: 700;
    color: #511278;
  }
  
  .filterInput {
    width: 300px;
    height: 25px;
  }
  
  .filterInput:focus {
    background-color: #f1e3fa;
  }